/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}